@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
    font-family: 'Poppins'sans-serif;
    background:#fff;
    color:  #000;
    height: 100vh;
    width: 100%;
}
.main{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4,300px);
    grid-template-rows: repeat(auto, 500px);
    grid-row-gap: 50px;
    grid-column-gap : 40px;
    position: relative;
    top: 10px;
    cursor: pointer;
}
.main img:hover{
    transition: .5s ease-in-out;
    transform: translateY(10px);
}
.main img, h4{
    margin-left: 35px;
    width: 250px;
    height: 400px;
    box-shadow: 0px 0px 20px rgba(0,0,0,0.9);
}
.main h4{
    height: 0;
}

@media screen and (max-width : 480px){
    .main{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(1,100%);
        grid-template-rows: repeat(auto, 500px);
        grid-row-gap: 30px;
        grid-column-gap : 0px;
        position: relative;
        top: 10px;
    }
    .main img{
        padding: 15px;
        margin-left: 0;
        width: 100%;
        height: 450px;
        box-shadow: none;
    }
    .main h4{
        position: relative;
        left: -20px;
        top: -8px;
    }
    
}
.searchBox{
    height: 80px;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    align-items: center;
    background: linear-gradient(rgba(255, 255, 0,0.8),rgba(0, 0, 0,0));
}
.searchBox h1 {
    letter-spacing: 2px;
    padding-left: 40px;
    color: red;
    font-size: 60px;
    font-weight: 900;
    text-shadow: 0px 0px 2px rgba(0,0,0,0.8);
    position: relative;
    cursor: pointer;
}
.fa-video{
    position: relative;
    left: -5px;
    top: -5.5px;
    font-size: 40px;
}
.searchBox input{
    position: relative;
    left: 30%;
    text-align: center;
    border: 2px solid lime;
    background: none;
    outline: none;
    height: 30px;
    border-radius: 35px;
    color: #000;
    font-weight: 500;
    cursor: pointer;
}
.searchBox ul{
    list-style: none;
    text-decoration: none;
    display: flex;
    position: relative;
    left: 810px;
    top: -3px;
}
.searchBox ul li{
    font-weight: 300 !important;
    margin-left: 30px;
    cursor: pointer;
}
.fa-search{
    position: relative;
    left: 380px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
@media screen and (max-width : 480px){
    .searchBox{
        height: 80px;
    }
    .searchBox h1{
        letter-spacing: 1px;
        font-size: 26px;
        padding-left: 20px;
        font-weight: 400 !important;
    }
    .searchBox input{
        position: relative;
        left: 25%;
        text-align: center;
        border: 2px solid lime;
        background: none;
        outline: none;
        height: 30px;
        border-radius: 35px;
        color: #000;
    } 
    .searchBox ul{
        display: none;
    }
    .fa-video{
        display: none;
    }
    .fa-search{
        display: none;
    }
}
.footer{
    width: 100%;
    height: 70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    line-height: 30px;
    background: linear-gradient(rgba(255, 255, 0,0.8),rgba(255, 255, 0,0.8));
}
.footer h4{
    background: none;
    box-shadow: none;
    height: 20px;
    margin-left: 80px;
}
.footer h5{
    position: relative;
    
}
.Preview{
    width: 100%;
    height: auto;
    margin-top: 50px;
    box-sizing: border-box;
   font-family: 'Poppins';
}
#FullPreview{
    display: grid;
    grid-template-columns: repeat(2,50%);
    padding: 20px;
    background: linear-gradient(#f9f871,#fff);
}
#content{
    line-height: 40px;
    letter-spacing: 1px;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#content p{
    font-size: 18px;
    font-weight: 400;
}
#content p span{
    font-size: 20px;
    font-weight: 800;
    color: #845ec2;
}
#FullPreview img{
    position: relative;
    left: 100px;
    height: 300px;
}
@media screen and (max-width : 480px){
    .Preview{
        width: 100%;
        height: auto;
        margin-top: 50px;
        box-sizing: border-box;
        font-family: 'Poppins';
    }
    #FullPreview{
        display: grid;
        grid-template-columns: 30%, 70%;
        padding: 10px;
    }
    #content{
        line-height: 20px;
        letter-spacing: .4px;
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    #content p{
        font-size: 14px;
        font-weight: 400;
    }
    #content p span{
        font-size: 16px;
        font-weight: 800;
        color: #845ec2;
    }
    #FullPreview img{
        position: relative;
        left: 10px;
        height: 200px;
    }
}
.container{
  font-family: 'Poppins',sans-serif;
  font-weight: 200 !important;
  box-sizing: border-box;
}
